  mounted(){
    //ソートの矢印が表示されるよう起動時にNoを昇順ソート
    this.mSortBy('id', 'check');
  },

  methods: {
gotoTop(){
  window.scrollTo({top: 0, behavior: 'smooth'});
},

    packCheck(id) {
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
      this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);
        }
      })
    },

    onKeyDown: function(e) {
      //改行でフォーカスを外す
      var active_element = document.activeElement;
      if(active_element){
        active_element.blur();
      }
    },

    filterChange:function(e) {
      //絞り込み文字列を変更
      this.searchWord = e.target.value;

      //ハイライト設定
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
      let reg1 = new RegExp("<mark>",'g');
      let reg2 = new RegExp("</mark>",'g');
      this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
      })
      if (e.target.value !== "") {
        this.packs.forEach(function(pack){
          let reg3 = new RegExp(e.target.value,'g');
          pack.content = pack.content.replace(reg3, "<mark>" + e.target.value + "</mark>");
        })
      }
      //alert("test")
    },

    mouseOverAction(index){
      var _this = this;
      let buf = "";
      this.packs.forEach(function(pack){
        if(pack.id == index) {
          buf = pack.content;
        }
      })

      let reg1 = new RegExp("<mark>",'g');
      let reg2 = new RegExp("</mark>",'g');
      buf = buf.replace(reg1, "");
      buf = buf.replace(reg2, "");
      (buf.match("ジュエル\\*")) ? this.tJewel = 'topics' : this.tJewel = '';
      (buf.match("高級募集チケット\\*")) ? this.tTicketLuxury = 'topics' : this.tTicketLuxury = '';
      (buf.match("通常募集チケット\\*")) ? this.tTicketRegular = 'topics' : this.tTicketRegular = '';
      (buf.match("企業モールド\\*")) ? this.tMoldCompany = 'topics' : this.tMoldCompany = '';
      (buf.match("ハイクオリティモールド\\*")) ? this.tMoldHigh = 'topics' : this.tMoldHigh = '';
      (buf.match("ミドルクオリティモールド\\*")) ? this.tMoldMiddle = 'topics' : this.tMoldMiddle = '';
      (buf.match("ニケ選択ボックス\\*")) ? this.tNikeSelect = 'topics' : this.tNikeSelect = '';
      (buf.match("コスチューム\\*")) ? this.tCostume = 'topics' : this.tCostume = '';
      (buf.match("クレジットケース\\*")) ? this.tCaseCredit = 'topics' : this.tCaseCredit = '';
      (buf.match("コアダストケース\\*")) ? this.tCaseCore = 'topics' : this.tCaseCore = '';
      (buf.match("バトルデータケース\\*")) ? this.tCaseBattle = 'topics' : this.tCaseBattle = '';
      (buf.match("クレジット1k\\*")) ? this.tCredit = 'topics' : this.tCredit = '';
      (buf.match("コアダスト\\*")) ? this.tCore = 'topics' : this.tCore = '';
      (buf.match("スキルマニュアルⅠ\\*")) ? this.tManualSkill1 = 'topics' : this.tManualSkill1 = '';
