      this.pJewel = 0;
      
      this.pTicketLuxury = 0;
      this.pTicketRegular = 0;
      this.pMoldCompany = 0;
      this.pMoldHigh = 0;
      this.pMoldMiddle = 0;
      this.pNikeSelect = 0;
      this.pCostume = 0;

      this.pCaseCredit = 0;
      this.pCaseCore = 0;
      this.pCaseBattle = 0;
      this.pCredit = 0;
      this.pCore = 0;
      
      this.pManualSkill1 = 0;
      this.pManualSkill2 = 0;
      this.pManualSkill3 = 0;
