  .packList td.Str-Wrap {
    text-align: left;
  }
  .asc::after {
    content: "↓"
  }
  .desc::after {
    content: "↑"
  }

  /* -----------------------------------------*/
  /* スマホなどの小さな画面向け設定 */
  /* -----------------------------------------*/
  @media screen and (max-width: 920px) {
    div.minFont{
      font-size: 0.5em; /* フォントサイズ（縮小OKなものだけに指定する） */
    }
    .packList td.division { /* パックリストの区分データ */
      min-width: 50px; 
    }
    .packList td.name { /* パックリストのパック名データ */
      min-width: 50px; 
    }
  }

  /* -----------------------------------------*/
  /* ツールチップ：ヘルプ */
  /* -----------------------------------------*/
  .tooltipHelp  {
    display: inline-block;
    position: relative;
  }
  .tooltipHelp svg {
    width: 20px;
    height: 20px;
    padding: 3px;
    border: 2px solid #c6cdd3;
    border-radius: 50%;
    box-sizing: border-box;
    cursor: pointer;
  }
  .tooltipHelp p {
    display: flex;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    bottom: -135px;
    left: 120px;
    transform: translateX(-50%);
    padding: .5em 1em;
    border-radius: 3px;
    background-color: #00000099;
    color: #fff;
    font-size: .7em;
    white-space: nowrap;
    transition: opacity .3s;
    text-align: left;
  }
  .tooltipHelp:hover > p {
    visibility: visible;
    opacity: 1;
  }
  .tooltipHelp p::before {
    position: absolute;
    top: 0px;
    width: 0px;
    height: 0px;
    background-color: inherit;
    clip-path: polygon(10% 0, 0 100%, 100% 100%);
    content: '';
  }


  .buttonTopUnder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border: none;
    border-radius: 50%;
    background-color: #2589d0;
  }
  .buttonTopUnder:hover {
    border: 1px solid #2589d0;
    background-color: #fff;
  }
  .buttonTopUnder:hover path {
    fill: #2589d0;
  }
  .posTop{
    position: absolute;
    z-index: 100;
    top: 170px;
    right: 40px;
  }
  .posUnder{
    position: absolute;
    z-index: 100;
    bottom: 4.5%;
    right: 40px;
  }
  .rollingV{
    transform: scale(1,-1);
  }
</style>


